

.strategies {
  padding: 30px 0;

  @include media-tablet {
    text-align: center;
  }
}

.strategies__title {
  margin: 0 0 14px;
}

.strategies__description {
  margin: 0 0 24px;
}

.strategies__tags {
  margin: 0 0 31px;

  @include media-tablet {
    justify-content: center;
  }
} 

// Strategy
.strategy-wrapper {
  >* {
    margin-bottom: 30px;

    @include media-tablet {
      flex-basis: 48%;
    }

    @include media-mobile {
      flex-basis: 100%;
    }
  }
}

.strategy {
  cursor: pointer;
  display: flex;
  flex-direction: column;
  position: relative;

  &_hidden {
    display: none;
  }

  @include media-tablet {
    overflow: hidden;
    padding-top: 200px;
  }
}

.strategy__content {
  padding: 24px 33px 26px;
  flex-grow: 1;
  display: flex;
  flex-direction: column;

  @include media-tablet {
    padding: 25px 28px 26px;
  }
}

.strategy__name {
  margin: 0 0 29px;
  text-align: left;
  flex-grow: 1;

  @include media-tablet {
    margin-bottom: 20px;
  }
}

.strategy__surname {
  padding: 0px;
}

.strategy__image {
  @include media-tablet {
    position: absolute;
    top: 0;
  }
}

.article-modal__content {
  .strategy__image {
    width: 100%;

    @include media-tablet {
      position: static;
    }
  }

  .strategy__date {
    @include font($size: 1.3rem, $weight: normal);
    margin: 0 0 5px;
  }

  .strategy__name {
    margin-bottom: 10px;
  }

  .strategy__text {
    margin: 0 0 12px;
  }
}