.button {
  @extend %font-prim;
  padding: 11px 25px 13px;
  font-style: normal;
  font-weight: 600;
  font-size: 1.6rem;
  line-height: 2.2rem;
  border-radius: 4px;
  cursor: pointer;
  transition: all 0.3s;

  &_bordered {
    background: $color-light;
    border: 1px solid $color-border;
    color: $color-text;

    &:hover {
      background: $color-hovered;
      border-color: $color-hovered;
    }
  }

  &_colored {
    background: $color-primary;
    color: $color-light;
    border: 1px solid $color-primary;

    &:hover {
      background: $color-additional;
      border-color: $color-additional;
    }
  }

  @include media-tablet {
    width: 170px;
  }
}

.link-button {
  display: inline-flex;
  align-items: center;
  color: $color-primary;
  @extend %font-add;

  &:hover {
    color: $color-additional;
    @extend %transition;
  }
}

.link-button__text {
  margin-left: 9px;
  @include font($size: 1.6rem, $height:2.4rem, $weight: 600);
}