.layout-4-column {
  @include columns($width: 21%);
}

.layout-3-column {
  @include columns($width: 30%);

  &:after {
    content: "";
    display: block;
    width: 30%;
  }
}

.layout-2-column {
  @include columns($width: 46%);
}

.layout-2-column-form {
  @include columns($width: 49%);
}